"use client";

import React, { useCallback, useEffect, useMemo, useRef, useState } from "react";

type Timeframe = "5m" | "15m" | "1h" | "4h" | "12h" | "1d" | "3d";
type UiMode = "scan" | "plan";

type ApiMode = "HTF" | "LTF" | "FULL" | "LTF_ONLY";

type AnalyzeResponse = {
  ok: boolean;
  error?: string;

  snapshot?: {
    symbol?: string; // resolved symbol from backend (e.g., BTCUSDT-PERP)
    price?: number;
    equity?: number;
    constraints?: any;
    timeframes?: Array<any>;
    live?: any;
  };

  gate?: {
    blocked: boolean;
    reasons: string[];
    strongHigherTrend: boolean;
    higherTimeframesUsed: string[];
    labels: string[];
  };

  plan?: {
    action: "NO_TRADE" | "LONG" | "SHORT";
    leverage: number;
    entry: { type: "market" | "limit" | "stop"; price?: number; trigger?: number };
    stop: { price: number; rationale: string };
    targets: Array<{ price: number; sizePct: number }>;
    riskPct: number;
    notes?: string;
    citations?: Array<{ rule_chunk_id: string }>;
  };

  execution?: {
    qty?: number;
    notionalUsd?: number;
    effectiveLeverage?: number;
    riskUsdPlanned?: number;
    riskUsdActual?: number;
    perTarget?: Array<{ idx: number; price: number; sizePct: number; pnlUsd?: number; rr?: number }>;
  };

  intraday?: any; // backend may return intraday execution plan in LTF modes

  verdict?: { ok: boolean; reasons: string[]; rr: number | null };
  intradayVerdict?: { ok: boolean; reasons: string[] };

  rules?: Array<{
    id: string;
    doc_name: string;
    page: number;
    section: string;
    chunk_type: string;
    similarity: number;
  }>;
};

type PersistedUI = {
  symbol: string;
  equityUsd: number;
  lookback: number;
  timeframesText: string;
  scanEveryMs: number;
  planEveryMs: number;
  autoRefresh: boolean;
  ltfOnly: boolean;
  aggressive: boolean;
  maxRiskPct: number;
  minRR: number;
};

const LS_KEY = "liveAnalyzerUI:v4";

function fmt(n: any, digits = 2) {
  const x = Number(n);
  return Number.isFinite(x) ? x.toFixed(digits) : "—";
}

function clamp(n: number, lo: number, hi: number) {
  return Math.max(lo, Math.min(hi, n));
}

function badgeStyle(action: string): React.CSSProperties {
  const base: React.CSSProperties = {
    padding: "6px 10px",
    borderRadius: 999,
    fontWeight: 850,
    display: "inline-flex",
    alignItems: "center",
    gap: 8,
    border: "1px solid rgba(255,255,255,0.14)",
    letterSpacing: 0.2,
  };

  if (action === "LONG") return { ...base, background: "rgba(0,255,0,0.10)" };
  if (action === "SHORT") return { ...base, background: "rgba(255,0,0,0.10)" };
  return { ...base, background: "rgba(255,255,255,0.06)" };
}

function pill(on: boolean): React.CSSProperties {
  return {
    padding: "8px 10px",
    borderRadius: 12,
    border: "1px solid rgba(255,255,255,0.16)",
    background: on ? "rgba(255,255,255,0.10)" : "transparent",
    cursor: "pointer",
    userSelect: "none",
    fontWeight: 800,
  };
}

function panel(): React.CSSProperties {
  return {
    padding: 12,
    borderRadius: 14,
    border: "1px solid rgba(255,255,255,0.12)",
    background: "rgba(255,255,255,0.03)",
  };
}

function parseTimeframes(input: string): { tfs: Timeframe[]; error?: string } {
  const allowed: Timeframe[] = ["5m", "15m", "1h", "4h", "12h", "1d", "3d"];
  const raw = input
    .split(",")
    .map((x) => x.trim())
    .filter(Boolean);

  const out: Timeframe[] = [];
  for (const tf of raw) {
    if (!allowed.includes(tf as Timeframe)) return { tfs: [], error: `Invalid timeframe: ${tf}` };
    out.push(tf as Timeframe);
  }
  if (!out.length) return { tfs: ["15m", "1h", "4h", "1d"] };
  return { tfs: out };
}

function isGptDriven(data: AnalyzeResponse | null) {
  const rules = data?.rules?.length ?? 0;
  const cites = data?.plan?.citations?.length ?? 0;
  return rules > 0 || cites > 0;
}

function safeTime(n?: number | null) {
  if (!n) return "—";
  try {
    return new Date(n).toLocaleTimeString();
  } catch {
    return "—";
  }
}

function msToCountdown(ms: number | null) {
  if (ms == null) return "—";
  const s = Math.max(0, Math.ceil(ms / 1000));
  const m = Math.floor(s / 60);
  const r = s % 60;
  if (m <= 0) return `${r}s`;
  return `${m}m ${r}s`;
}

function modeLabel(apiMode: ApiMode) {
  if (apiMode === "LTF_ONLY") return "LTF-ONLY";
  return apiMode;
}

export default function LiveAnalyzer() {
  const [symbol, setSymbol] = useState("BTCUSDT");
  const [equityUsd, setEquityUsd] = useState(100);
  const [lookback, setLookback] = useState(300);

  // scan frequently + plan hourly
  const [scanEveryMs, setScanEveryMs] = useState(5 * 60 * 1000); // 5 min
  const [planEveryMs, setPlanEveryMs] = useState(60 * 60 * 1000); // 60 min
  const [autoRefresh, setAutoRefresh] = useState(true);

  // NEW: ignore HTF gate + run LTF execution mode
  const [ltfOnly, setLtfOnly] = useState(false);

  // NEW: allow more aggressive planning (backend-controlled bypasses)
  const [aggressive, setAggressive] = useState(false);

  // Constraints (sent to backend)
  const [maxRiskPct, setMaxRiskPct] = useState(1);
  const [minRR, setMinRR] = useState(1.8);

  const [timeframesText, setTimeframesText] = useState("1h,4h,12h,1d");
  const [timeframes, setTimeframes] = useState<Timeframe[]>(["1h", "4h", "12h", "1d"]);
  const [tfError, setTfError] = useState<string | null>(null);

  const [data, setData] = useState<AnalyzeResponse | null>(null);
  const [loading, setLoading] = useState(false);

  const [lastScanAt, setLastScanAt] = useState<number | null>(null);
  const [lastPlanAt, setLastPlanAt] = useState<number | null>(null);

  const [nextScanInMs, setNextScanInMs] = useState<number | null>(null);
  const [nextPlanInMs, setNextPlanInMs] = useState<number | null>(null);

  const abortRef = useRef<AbortController | null>(null);
  const inflightRef = useRef(false);
  const reqIdRef = useRef(0);

  const scanStartRef = useRef<number>(0);
  const planStartRef = useRef<number>(0);

  // Load persisted UI
  useEffect(() => {
    try {
      const raw = localStorage.getItem(LS_KEY);
      if (!raw) return;
      const v = JSON.parse(raw) as Partial<PersistedUI>;
      if (v.symbol) setSymbol(v.symbol);
      if (typeof v.equityUsd === "number") setEquityUsd(v.equityUsd);
      if (typeof v.lookback === "number") setLookback(v.lookback);
      if (typeof v.timeframesText === "string") setTimeframesText(v.timeframesText);
      if (typeof v.scanEveryMs === "number") setScanEveryMs(v.scanEveryMs);
      if (typeof v.planEveryMs === "number") setPlanEveryMs(v.planEveryMs);
      if (typeof v.autoRefresh === "boolean") setAutoRefresh(v.autoRefresh);
      if (typeof v.ltfOnly === "boolean") setLtfOnly(v.ltfOnly);
      if (typeof v.aggressive === "boolean") setAggressive(v.aggressive);
      if (typeof v.maxRiskPct === "number") setMaxRiskPct(v.maxRiskPct);
      if (typeof v.minRR === "number") setMinRR(v.minRR);
    } catch {
      // ignore
    }
  }, []);

  // Persist UI state
  useEffect(() => {
    const payload: PersistedUI = {
      symbol,
      equityUsd,
      lookback,
      timeframesText,
      scanEveryMs,
      planEveryMs,
      autoRefresh,
      ltfOnly,
      aggressive,
      maxRiskPct,
      minRR,
    };
    try {
      localStorage.setItem(LS_KEY, JSON.stringify(payload));
    } catch {
      // ignore
    }
  }, [symbol, equityUsd, lookback, timeframesText, scanEveryMs, planEveryMs, autoRefresh, ltfOnly, aggressive, maxRiskPct, minRR]);

  // Parse timeframes
  useEffect(() => {
    const { tfs, error } = parseTimeframes(timeframesText);
    setTfError(error ?? null);
    if (!error) setTimeframes(tfs);
  }, [timeframesText]);

  const baseBody = useMemo(
    () => ({
      symbol,
      timeframes,
      lookback,
      equityUsd,
      aggressive,
      constraints: { maxLeverage: 10, maxRiskPct, minRR },
    }),
    [symbol, timeframes, lookback, equityUsd, aggressive, maxRiskPct, minRR]
  );

  const getApiMode = useCallback(
    (uiMode: UiMode): ApiMode => {
    // For "plan", use LTF_ONLY if toggled; otherwise HTF.
    // For "scan", keep it HTF so you can see gate state without burning model costs.
      if (uiMode === "plan") return ltfOnly ? "LTF_ONLY" : "HTF";
      return "HTF";
    },
    [ltfOnly]
  );

  const callAnalyze = useCallback(
    async (uiMode: UiMode) => {
    if (inflightRef.current) return;
    inflightRef.current = true;

    abortRef.current?.abort();
    const ac = new AbortController();
    abortRef.current = ac;

    const myReqId = ++reqIdRef.current;

    setLoading(true);
    try {
      const apiMode = getApiMode(uiMode);

      const body =
        uiMode === "plan"
          ? {
              ...baseBody,
              intent: "trade",
              mode: apiMode, // "HTF" | "LTF_ONLY"
              ltfPlan: apiMode === "LTF_ONLY" ? { enabled: true, timeframes: ["4h", "1h", "15m"], minBars: 120 } : undefined,
              wsProbe: { durationMs: 2000, maxMessages: 800 },
            }
          : {
              ...baseBody,
              // keep scans cheap: make it explicit for the backend
              intent: "explain",
              aggressive: false,
              mode: apiMode, // "HTF"
              // cheap scan: skip WS by setting duration=0
              wsProbe: { durationMs: 0, maxMessages: 0 },
            };

      const res = await fetch("/api/analyze", {
        method: "POST",
        headers: { "content-type": "application/json" },
        body: JSON.stringify(body),
        signal: ac.signal,
      });

      let json: AnalyzeResponse | null = null;
      let rawText: string | null = null;

      try {
        json = (await res.json()) as AnalyzeResponse;
      } catch {
        try {
          rawText = await res.text();
        } catch {
          rawText = null;
        }
      }

      if (myReqId !== reqIdRef.current) return;

      if (!res.ok) {
        const errMsg =
          json?.error ??
          (rawText && rawText.trim() ? rawText.slice(0, 200) : null) ??
          `HTTP ${res.status}`;
        setData({ ok: false, error: errMsg });
      } else {
        // If backend returned non-JSON but res.ok, treat as error.
        if (!json) {
          const errMsg =
            (rawText && rawText.trim() ? rawText.slice(0, 200) : null) ?? "Non-JSON response from /api/analyze";
          setData({ ok: false, error: errMsg });
        } else {
          setData(json);
        }
      }

      const now = Date.now();
      if (uiMode === "scan") setLastScanAt(now);
      if (uiMode === "plan") setLastPlanAt(now);
    } catch (e: any) {
      if (e?.name !== "AbortError") {
        setData({ ok: false, error: e?.message ?? "Request failed" });
        const now = Date.now();
        if (uiMode === "scan") setLastScanAt(now);
        if (uiMode === "plan") setLastPlanAt(now);
      }
    } finally {
      setLoading(false);
      inflightRef.current = false;
    }
    },
    [baseBody, getApiMode]
  );

  // Dual loop: scan fast, plan slow
  useEffect(() => {
    if (!autoRefresh) {
      setNextScanInMs(null);
      setNextPlanInMs(null);
      return;
    }

    const scanMs = clamp(Number.isFinite(scanEveryMs) ? scanEveryMs : 60_000, 3_000, 60 * 60 * 1000);
    const planMs = clamp(Number.isFinite(planEveryMs) ? planEveryMs : 60 * 60 * 1000, 30_000, 24 * 60 * 60 * 1000);

    scanStartRef.current = Date.now();
    planStartRef.current = Date.now();

    // Run immediately: do a scan right away, then schedule plan
    callAnalyze("scan");

    const tickEvery = 250;
    const interval = setInterval(() => {
      const now = Date.now();

      const scanElapsed = now - scanStartRef.current;
      const planElapsed = now - planStartRef.current;

      const scanRemaining = Math.max(0, scanMs - scanElapsed);
      const planRemaining = Math.max(0, planMs - planElapsed);

      setNextScanInMs(scanRemaining);
      setNextPlanInMs(planRemaining);

      // If plan is due, it has priority over scan
      if (planRemaining <= 0) {
        planStartRef.current = Date.now();
        callAnalyze("plan");
        return;
      }

      if (scanRemaining <= 0) {
        scanStartRef.current = Date.now();
        callAnalyze("scan");
      }
    }, tickEvery);

    return () => {
      clearInterval(interval);
      abortRef.current?.abort();
    };
  }, [autoRefresh, scanEveryMs, planEveryMs, callAnalyze]);

  const plan = data?.plan;
  const snap = data?.snapshot;
  const gate = data?.gate;
  const verdict = data?.verdict;
  const exec = data?.execution;

  const price = snap?.price;
  const gptFlag = isGptDriven(data);

  const ws = snap?.live?.ws;
  const tfSummaries: Array<any> = Array.isArray(snap?.timeframes) ? (snap!.timeframes as any[]) : [];

  const resolvedSymbol = snap?.symbol || "";
  const effectivePlanMode = getApiMode("plan");

  return (
    <div style={{ display: "grid", gap: 12 }}>
      {/* Controls */}
      <div style={{ ...panel(), display: "grid", gap: 10, gridTemplateColumns: "1fr 1fr 1fr 1fr" }}>
        <label style={{ display: "grid", gap: 6 }}>
          <span style={{ opacity: 0.8 }}>Symbol</span>
          <input
            value={symbol}
            onChange={(e) => setSymbol(e.target.value)}
            style={{ padding: 10, borderRadius: 12 }}
            spellCheck={false}
          />
          {resolvedSymbol && resolvedSymbol !== symbol ? (
            <span style={{ opacity: 0.65, fontSize: 12 }}>Resolved: {resolvedSymbol}</span>
          ) : (
            <span style={{ opacity: 0.65, fontSize: 12 }}>Resolved: —</span>
          )}
        </label>

        <label style={{ display: "grid", gap: 6 }}>
          <span style={{ opacity: 0.8 }}>Equity (paper)</span>
          <input
            value={equityUsd}
            onChange={(e) => setEquityUsd(Number(e.target.value))}
            type="number"
            min={0}
            style={{ padding: 10, borderRadius: 12 }}
          />
        </label>

        <label style={{ display: "grid", gap: 6 }}>
          <span style={{ opacity: 0.8 }}>Max risk %</span>
          <input
            value={maxRiskPct}
            onChange={(e) => setMaxRiskPct(Number(e.target.value))}
            type="number"
            min={0.1}
            max={10}
            step={0.1}
            style={{ padding: 10, borderRadius: 12 }}
          />
          <span style={{ opacity: 0.65, fontSize: 12 }}>Backend clamps; scan uses cheap mode</span>
        </label>

        <label style={{ display: "grid", gap: 6 }}>
          <span style={{ opacity: 0.8 }}>Min RR</span>
          <input
            value={minRR}
            onChange={(e) => setMinRR(Number(e.target.value))}
            type="number"
            min={0.5}
            max={10}
            step={0.1}
            style={{ padding: 10, borderRadius: 12 }}
          />
          <span style={{ opacity: 0.65, fontSize: 12 }}>Applied to verdict + target sanity</span>
        </label>

        <label style={{ display: "grid", gap: 6 }}>
          <span style={{ opacity: 0.8 }}>Scan every (ms)</span>
          <input
            value={scanEveryMs}
            onChange={(e) => setScanEveryMs(Number(e.target.value))}
            type="number"
            min={3000}
            step={1000}
            style={{ padding: 10, borderRadius: 12 }}
          />
          <span style={{ opacity: 0.65, fontSize: 12 }}>Next scan: {msToCountdown(nextScanInMs)}</span>
        </label>

        <label style={{ display: "grid", gap: 6 }}>
          <span style={{ opacity: 0.8 }}>Plan every (ms)</span>
          <input
            value={planEveryMs}
            onChange={(e) => setPlanEveryMs(Number(e.target.value))}
            type="number"
            min={30_000}
            step={1000}
            style={{ padding: 10, borderRadius: 12 }}
          />
          <span style={{ opacity: 0.65, fontSize: 12 }}>Next plan: {msToCountdown(nextPlanInMs)}</span>
        </label>

        <label style={{ display: "grid", gap: 6 }}>
          <span style={{ opacity: 0.8 }}>Lookback</span>
          <input
            value={lookback}
            onChange={(e) => setLookback(Number(e.target.value))}
            type="number"
            min={80}
            max={1500}
            step={10}
            style={{ padding: 10, borderRadius: 12 }}
          />
        </label>

        <div style={{ display: "grid", gap: 6, gridColumn: "span 3" }}>
          <span style={{ opacity: 0.8 }}>Timeframes (comma-separated)</span>
          <input
            value={timeframesText}
            onChange={(e) => setTimeframesText(e.target.value)}
            style={{
              padding: 10,
              borderRadius: 12,
              border: tfError ? "1px solid rgba(255,0,0,0.55)" : "1px solid rgba(255,255,255,0.12)",
            }}
            spellCheck={false}
          />
          {tfError ? <div style={{ color: "rgba(255,120,120,0.95)" }}>{tfError}</div> : null}
        </div>

        <div style={{ gridColumn: "span 4", display: "flex", gap: 10, flexWrap: "wrap", alignItems: "center" }}>
          <button type="button" onClick={() => setAutoRefresh((v) => !v)} style={pill(autoRefresh)}>
            {autoRefresh ? "Auto-refresh: ON" : "Auto-refresh: OFF"}
          </button>

          <label style={{ display: "inline-flex", gap: 10, alignItems: "center", padding: "8px 10px", borderRadius: 12, border: "1px solid rgba(255,255,255,0.16)" }}>
            <input
              type="checkbox"
              checked={ltfOnly}
              onChange={(e) => setLtfOnly(e.target.checked)}
              style={{ width: 16, height: 16 }}
            />
            <span style={{ fontWeight: 900 }}>LTF-only</span>
            <span style={{ opacity: 0.7, fontSize: 12 }}>(ignore HTF gate for plan)</span>
          </label>

          <label style={{ display: "inline-flex", gap: 10, alignItems: "center", padding: "8px 10px", borderRadius: 12, border: "1px solid rgba(255,255,255,0.16)" }}>
            <input
              type="checkbox"
              checked={aggressive}
              onChange={(e) => setAggressive(e.target.checked)}
              style={{ width: 16, height: 16 }}
            />
            <span style={{ fontWeight: 900 }}>Aggressive</span>
            <span style={{ opacity: 0.7, fontSize: 12 }}>(relaxes gates; higher churn)</span>
          </label>

          <button
            type="button"
            onClick={() => callAnalyze("scan")}
            style={{
              padding: "10px 12px",
              borderRadius: 12,
              border: "1px solid rgba(255,255,255,0.16)",
              cursor: "pointer",
              fontWeight: 900,
            }}
            title="Runs cheap scan now"
          >
            {loading ? "Working…" : "Run scan now"}
          </button>

          <button
            type="button"
            onClick={() => callAnalyze("plan")}
            style={{
              padding: "10px 12px",
              borderRadius: 12,
              border: "1px solid rgba(255,255,255,0.16)",
              cursor: "pointer",
              fontWeight: 900,
            }}
            title="Runs full engine now (ws + rag + model). If LTF-only is enabled, bypasses HTF gate."
          >
            {loading ? "Working…" : `Run full plan now (${modeLabel(effectivePlanMode)})`}
          </button>

          <div style={{ marginLeft: "auto", opacity: 0.75, fontSize: 12, lineHeight: 1.4 }}>
            <div>Last scan: {safeTime(lastScanAt)}</div>
            <div>Last plan: {safeTime(lastPlanAt)}</div>
          </div>
        </div>
      </div>

      {/* Headline */}
      <div style={{ ...panel(), display: "grid", gap: 10 }}>
        <div style={{ display: "flex", justifyContent: "space-between", alignItems: "baseline", gap: 12 }}>
          <div style={{ display: "flex", gap: 12, alignItems: "center", flexWrap: "wrap" }}>
            <span style={{ opacity: 0.8 }}>Price</span>
            <span style={{ fontSize: 22, fontWeight: 950 }}>{Number.isFinite(Number(price)) ? price : "—"}</span>

            <span style={badgeStyle(plan?.action ?? "—")}>
              <span>{plan?.action ?? "—"}</span>
              <span style={{ opacity: 0.7, fontWeight: 850, fontSize: 12 }}>{gptFlag ? "GPT/RAG" : "scan-only"}</span>
            </span>

            {gate ? (
              <span style={badgeStyle(gate.blocked ? "NO_TRADE" : "LONG")}>
                <span>{gate.blocked ? "GATE: BLOCKED" : "GATE: PASS"}</span>
                <span style={{ opacity: 0.7, fontWeight: 850, fontSize: 12 }}>
                  {gate.higherTimeframesUsed?.join(" + ") ?? "—"}
                </span>
              </span>
            ) : null}

            {ltfOnly ? (
              <span style={badgeStyle("NO_TRADE")}>
                <span>LTF-ONLY</span>
                <span style={{ opacity: 0.7, fontWeight: 850, fontSize: 12 }}>HTF gate bypass for plan</span>
              </span>
            ) : null}
          </div>

          <div style={{ opacity: 0.75, textAlign: "right" }}>
            <div>Last response: {safeTime(Math.max(lastScanAt ?? 0, lastPlanAt ?? 0) || null)}</div>
            <div style={{ opacity: 0.65, fontSize: 12 }}>
              scan={Math.round(clamp(scanEveryMs, 3000, 3_600_000) / 60000)}m · plan=
              {Math.round(clamp(planEveryMs, 30_000, 86_400_000) / 60000)}m
            </div>
          </div>
        </div>

        {data && !data.ok ? (
          <div style={{ padding: 10, borderRadius: 12, background: "rgba(255,0,0,0.10)" }}>
            <strong>Error:</strong> {data.error ?? "Unknown"}
          </div>
        ) : null}

        {/* Gate reasons */}
        {gate?.blocked && gate.reasons?.length ? (
          <div style={{ padding: 10, borderRadius: 12, background: "rgba(255,200,0,0.08)" }}>
            <div style={{ fontWeight: 900, marginBottom: 6 }}>
              HTF Regime Gate {ltfOnly ? "(BLOCKED — bypassed for LTF-only plan)" : "(BLOCKED)"}
            </div>
            <ul style={{ margin: 0, paddingLeft: 18, opacity: 0.95 }}>
              {gate.reasons.map((r, i) => (
                <li key={i}>{r}</li>
              ))}
            </ul>
          </div>
        ) : null}

        {/* Plan grid */}
        {plan ? (
          <div style={{ display: "grid", gap: 10 }}>
            <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 1fr", gap: 10 }}>
              <div style={panel()}>
                <div style={{ opacity: 0.8 }}>Leverage</div>
                <div style={{ fontWeight: 950, fontSize: 18 }}>{plan.leverage}x</div>
              </div>

              <div style={panel()}>
                <div style={{ opacity: 0.8 }}>Risk</div>
                <div style={{ fontWeight: 950, fontSize: 18 }}>{fmt(plan.riskPct, 2)}%</div>
              </div>

              <div style={panel()}>
                <div style={{ opacity: 0.8 }}>RR (T1)</div>
                <div style={{ fontWeight: 950, fontSize: 18 }}>{verdict?.rr == null ? "—" : fmt(verdict.rr, 2)}</div>
                {verdict?.reasons?.length ? (
                  <div style={{ marginTop: 6, opacity: 0.75, fontSize: 12 }}>{verdict.reasons.join(" · ")}</div>
                ) : null}
              </div>
            </div>

            <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gap: 10 }}>
              <div style={panel()}>
                <div style={{ fontWeight: 950 }}>Entry</div>
                <div style={{ marginTop: 6, opacity: 0.95 }}>
                  {plan.entry.type.toUpperCase()} {plan.entry.price != null ? `@ ${fmt(plan.entry.price, 2)}` : ""}
                  {plan.entry.trigger != null ? ` (trigger ${fmt(plan.entry.trigger, 2)})` : ""}
                </div>
              </div>

              <div style={panel()}>
                <div style={{ fontWeight: 950 }}>Stop</div>
                <div style={{ marginTop: 6, opacity: 0.95 }}>
                  {fmt(plan.stop.price, 2)} — {plan.stop.rationale}
                </div>
              </div>
            </div>

            <div style={panel()}>
              <div style={{ fontWeight: 950 }}>Targets</div>
              <div style={{ marginTop: 8, display: "grid", gap: 6 }}>
                {plan.targets?.map((t, i) => (
                  <div key={i} style={{ display: "flex", justifyContent: "space-between", opacity: 0.95 }}>
                    <span>
                      T{i + 1}: {fmt(t.price, 2)}
                    </span>
                    <span style={{ fontWeight: 900 }}>{fmt(t.sizePct, 0)}%</span>
                  </div>
                ))}
              </div>
            </div>

            {exec && (exec.qty != null || exec.notionalUsd != null) ? (
              <div style={panel()}>
                <div style={{ display: "flex", justifyContent: "space-between", alignItems: "baseline", gap: 12 }}>
                  <div style={{ fontWeight: 950 }}>Execution sizing</div>
                  <div style={{ opacity: 0.7, fontSize: 12 }}>paper=${fmt(equityUsd, 0)} · cap=10x</div>
                </div>

                <div style={{ marginTop: 10, display: "grid", gridTemplateColumns: "1fr 1fr 1fr 1fr", gap: 10 }}>
                  <div style={{ ...panel(), background: "rgba(255,255,255,0.02)" }}>
                    <div style={{ opacity: 0.8 }}>Qty</div>
                    <div style={{ fontWeight: 950, fontSize: 18 }}>{exec.qty == null ? "—" : fmt(exec.qty, 6)}</div>
                  </div>
                  <div style={{ ...panel(), background: "rgba(255,255,255,0.02)" }}>
                    <div style={{ opacity: 0.8 }}>Notional</div>
                    <div style={{ fontWeight: 950, fontSize: 18 }}>${exec.notionalUsd == null ? "—" : fmt(exec.notionalUsd, 2)}</div>
                  </div>
                  <div style={{ ...panel(), background: "rgba(255,255,255,0.02)" }}>
                    <div style={{ opacity: 0.8 }}>Eff. leverage</div>
                    <div style={{ fontWeight: 950, fontSize: 18 }}>{exec.effectiveLeverage == null ? "—" : fmt(exec.effectiveLeverage, 2)}x</div>
                  </div>
                  <div style={{ ...panel(), background: "rgba(255,255,255,0.02)" }}>
                    <div style={{ opacity: 0.8 }}>Risk ($)</div>
                    <div style={{ fontWeight: 950, fontSize: 18 }}>${exec.riskUsdActual == null ? "—" : fmt(exec.riskUsdActual, 2)}</div>
                    {exec.riskUsdPlanned != null ? (
                      <div style={{ opacity: 0.7, fontSize: 12 }}>planned ${fmt(exec.riskUsdPlanned, 2)}</div>
                    ) : null}
                  </div>
                </div>

                {Array.isArray(exec.perTarget) && exec.perTarget.length ? (
                  <div style={{ marginTop: 10 }}>
                    <div style={{ fontWeight: 950, marginBottom: 6 }}>Per-target PnL</div>
                    <div style={{ overflowX: "auto" }}>
                      <table style={{ width: "100%", borderCollapse: "collapse" }}>
                        <thead>
                          <tr style={{ textAlign: "left", opacity: 0.75 }}>
                            <th style={{ padding: "8px 6px" }}>Target</th>
                            <th style={{ padding: "8px 6px" }}>Price</th>
                            <th style={{ padding: "8px 6px" }}>Size</th>
                            <th style={{ padding: "8px 6px" }}>PnL ($)</th>
                            <th style={{ padding: "8px 6px" }}>RR</th>
                          </tr>
                        </thead>
                        <tbody>
                          {exec.perTarget.map((t, i) => (
                            <tr key={i} style={{ borderTop: "1px solid rgba(255,255,255,0.08)" }}>
                              <td style={{ padding: "10px 6px", fontWeight: 900 }}>T{(t.idx ?? i) + 1}</td>
                              <td style={{ padding: "10px 6px" }}>{fmt(t.price, 2)}</td>
                              <td style={{ padding: "10px 6px" }}>{fmt(t.sizePct, 0)}%</td>
                              <td style={{ padding: "10px 6px" }}>{t.pnlUsd == null ? "—" : `$${fmt(t.pnlUsd, 2)}`}</td>
                              <td style={{ padding: "10px 6px" }}>{t.rr == null ? "—" : fmt(t.rr, 2)}</td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                  </div>
                ) : null}
              </div>
            ) : null}

            {plan.notes ? (
              <div style={{ ...panel(), background: "rgba(255,255,255,0.04)" }}>
                <div style={{ fontWeight: 950 }}>Notes</div>
                <div style={{ marginTop: 8, whiteSpace: "pre-wrap", opacity: 0.92 }}>{plan.notes}</div>
              </div>
            ) : null}

            {plan.citations?.length ? (
              <div style={panel()}>
                <div style={{ fontWeight: 950 }}>Citations (rule chunks)</div>
                <div style={{ marginTop: 8, display: "grid", gap: 6 }}>
                  {plan.citations.map((c, i) => (
                    <div key={i} style={{ opacity: 0.95 }}>
                      • {c.rule_chunk_id}
                    </div>
                  ))}
                </div>
              </div>
            ) : null}
          </div>
        ) : null}
      </div>

      {/* TF table */}
      {tfSummaries.length ? (
        <div style={panel()}>
          <div style={{ display: "flex", justifyContent: "space-between", alignItems: "baseline", gap: 12 }}>
            <div style={{ fontWeight: 950, fontSize: 16 }}>Timeframes</div>
            <div style={{ opacity: 0.7, fontSize: 12 }}>Regime2 label + scores; EMA/RSI last candle</div>
          </div>

          <div style={{ marginTop: 10, overflowX: "auto" }}>
            <table style={{ width: "100%", borderCollapse: "collapse" }}>
              <thead>
                <tr style={{ textAlign: "left", opacity: 0.75 }}>
                  <th style={{ padding: "8px 6px" }}>TF</th>
                  <th style={{ padding: "8px 6px" }}>Price</th>
                  <th style={{ padding: "8px 6px" }}>Δ</th>
                  <th style={{ padding: "8px 6px" }}>EMA20</th>
                  <th style={{ padding: "8px 6px" }}>EMA50</th>
                  <th style={{ padding: "8px 6px" }}>RSI14</th>
                  <th style={{ padding: "8px 6px" }}>Regime2</th>
                  <th style={{ padding: "8px 6px" }}>Trend</th>
                  <th style={{ padding: "8px 6px" }}>Chop</th>
                </tr>
              </thead>
              <tbody>
                {tfSummaries.map((r: any, idx: number) => {
                  if (!r?.ok) {
                    return (
                      <tr key={idx} style={{ borderTop: "1px solid rgba(255,255,255,0.08)" }}>
                        <td style={{ padding: "10px 6px", fontWeight: 900 }}>{r?.tf ?? "—"}</td>
                        <td style={{ padding: "10px 6px" }} colSpan={8}>
                          <span style={{ opacity: 0.85 }}>NOT OK</span>{" "}
                          <span style={{ opacity: 0.65 }}>· {r?.reason ?? "unknown"} · rows={r?.rows ?? "—"}</span>
                        </td>
                      </tr>
                    );
                  }

                  const ind = r.indicators ?? null;
                  const ema = r.ema ?? null;
                  const reg2 = r.regime2 ?? {};
                  const ema20 = (ind?.ema20 ?? ema?.ema20) ?? null;
                  const ema50 = (ind?.ema50 ?? ema?.ema50) ?? null;
                  const rsi14 = (ind?.rsi14 ?? r.rsi14) ?? null;
                  const delta = (r.delta ?? (r.ohlc ? (Number(r.ohlc.close) - Number(r.ohlc.open)) : null)) ?? null;
                  return (
                    <tr key={idx} style={{ borderTop: "1px solid rgba(255,255,255,0.08)" }}>
                      <td style={{ padding: "10px 6px", fontWeight: 950 }}>{r.tf}</td>
                      <td style={{ padding: "10px 6px" }}>{fmt(r.price, 2)}</td>
                      <td style={{ padding: "10px 6px" }}>{fmt(delta, 2)}</td>
                      <td style={{ padding: "10px 6px" }}>{fmt(ema20, 2)}</td>
                      <td style={{ padding: "10px 6px" }}>{fmt(ema50, 2)}</td>
                      <td style={{ padding: "10px 6px" }}>{fmt(rsi14, 1)}</td>
                      <td style={{ padding: "10px 6px", fontWeight: 900 }}>{reg2.label ?? "—"}</td>
                      <td style={{ padding: "10px 6px" }}>{fmt(reg2.trendScore, 0)}</td>
                      <td style={{ padding: "10px 6px" }}>{fmt(reg2.chopScore, 0)}</td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
        </div>
      ) : null}

      {/* Live probe */}
      {ws ? (
        <div style={panel()}>
          <div style={{ fontWeight: 950 }}>Live tape probe (only present if backend ran WS)</div>
          <div style={{ marginTop: 8, display: "grid", gap: 6, opacity: 0.92 }}>
            <div>
              msgs={ws.messages} · ia_p95={fmt(ws.interArrival?.p95, 1)}ms · delay_p95={fmt(ws.ingestDelay?.p95, 1)}ms
            </div>
            <div>
              buy={fmt(ws.flow?.buy, 2)} · sell={fmt(ws.flow?.sell, 2)} · imbalance={fmt(ws.flow?.imbalance, 4)}
            </div>
          </div>
        </div>
      ) : null}

      {/* RAG sources */}
      {data?.rules?.length ? (
        <div style={panel()}>
          <details>
            <summary style={{ cursor: "pointer", fontWeight: 950 }}>RAG sources (top {data.rules.length})</summary>
            <div style={{ marginTop: 10, display: "grid", gap: 8 }}>
              {data.rules.map((r) => (
                <div
                  key={r.id}
                  style={{
                    padding: 10,
                    borderRadius: 12,
                    border: "1px solid rgba(255,255,255,0.10)",
                    background: "rgba(255,255,255,0.02)",
                  }}
                >
                  <div style={{ fontWeight: 950 }}>{r.doc_name}</div>
                  <div style={{ opacity: 0.8, marginTop: 4 }}>
                    page {r.page} · {r.section} · {r.chunk_type} · sim {fmt(r.similarity, 3)}
                  </div>
                </div>
              ))}
            </div>
          </details>
        </div>
      ) : null}
    </div>
  );
}