"use client";

import * as React from "react";

type ChatIntent = "explain" | "trade";

export default function LiveChat(props: {
  symbol: string;
  timeframes: string[];
  lookback: number;
}) {
  const [question, setQuestion] = React.useState("");
  const [intent, setIntent] = React.useState<ChatIntent>("explain");
  const [ltfOnly, setLtfOnly] = React.useState(false);

  const [answer, setAnswer] = React.useState<string | null>(null);
  const [tradeResult, setTradeResult] = React.useState<any | null>(null);

  const [loading, setLoading] = React.useState(false);
  const [error, setError] = React.useState<string | null>(null);

  const formatTradeBrief = React.useCallback((resp: any): string => {
    // resp is either the /api/chat wrapper { intent, forwarded, plan: analyzeJson }
    // OR directly analyzeJson (in case your server changes later)
    const analyze = resp?.plan ?? resp;
    const forwardedMode = resp?.forwarded?.mode ?? analyze?.mode ?? (ltfOnly ? "LTF_ONLY" : "HTF");

    const snap = analyze?.snapshot;
    const verdict = analyze?.verdict;
    const planObj = analyze?.plan ?? analyze; // analyzeJson includes `plan`

    if (!analyze) return "No trade response returned.";
    if (!planObj) return "No plan returned.";

    const lines: string[] = [];

    lines.push(`Symbol: ${snap?.symbol ?? "?"}`);
    lines.push(`Mode: ${forwardedMode}`);

    const gate = snap?.gate ?? analyze?.gate;
    if (gate) {
      lines.push(
        `HTF Gate: ${gate.blocked ? "BLOCKED" : "PASS"} (${(gate.higherTimeframesUsed || []).join(", ")})`
      );
      if (gate.reasons?.length) lines.push(`Gate reasons: ${gate.reasons.join(" | ")}`);
    }

    lines.push("");
    lines.push(`Action: ${planObj.action ?? "?"}`);
    lines.push(`Leverage: ${planObj.leverage ?? "?"}x`);

    if (planObj.entry) {
      const entryType = planObj.entry.type ?? "market";
      const entryPrice = planObj.entry.price ?? "market";
      lines.push(`Entry: ${entryType} @ ${entryPrice} (trigger ${planObj.entry.trigger ?? "n/a"})`);
    }

    if (planObj.stop) {
      lines.push(`Stop: ${planObj.stop.price ?? "?"} — ${planObj.stop.rationale ?? ""}`.trim());
    }

    if (Array.isArray(planObj.targets) && planObj.targets.length) {
      lines.push("Targets:");
      for (const t of planObj.targets) lines.push(`- ${t.price} (${t.sizePct ?? "?"}%)`);
    }

    if (planObj.riskPct != null) lines.push(`Risk: ${planObj.riskPct}%`);
    if (planObj.notes) lines.push(`Notes: ${planObj.notes}`);

    if (verdict) {
      lines.push("");
      lines.push(
        `Validator: ${verdict.ok ? "OK" : "FAIL"}${
          verdict.rr != null ? ` | RR=${Number(verdict.rr).toFixed(2)}` : ""
        }`
      );
      if (verdict.reasons?.length) lines.push(`Reasons: ${verdict.reasons.join(" | ")}`);
    }

    return lines.join("\n");
  }, [ltfOnly]);

  async function ask() {
    const q = question.trim();
    if (!q) return;

    setLoading(true);
    setError(null);
    setAnswer(null);
    setTradeResult(null);

    try {
      const res = await fetch("/api/chat", {
        method: "POST",
        headers: { "content-type": "application/json" },
        body: JSON.stringify({
          intent,
          symbol: props.symbol,
          timeframes: props.timeframes,
          lookback: props.lookback,
          question: q,

          tradeMode: ltfOnly ? "LTF_ONLY" : "HTF",
          equityUsd: 100,
          ltfPlan: ltfOnly ? { allow: true, direction: "both" } : undefined,
          wsProbe: { durationMs: 5000, maxMessages: 2000 },

          doc_names: null,
          chunk_types: null,
        }),
      });

      const j = await res.json();
      if (!res.ok) throw new Error(j?.error || `HTTP ${res.status}`);

      if (intent === "trade") {
        // KEEP wrapper so forwarded.mode remains available
        setTradeResult(j);
      } else {
        setAnswer(j.answer ?? "(no answer)");
      }
    } catch (e: any) {
      setError(e?.message || "Unknown error");
    } finally {
      setLoading(false);
    }
  }

  return (
    <div
      style={{
        border: "1px solid rgba(255,255,255,0.12)",
        borderRadius: 10,
        padding: 12,
      }}
    >
      <div style={{ fontWeight: 800, marginBottom: 8 }}>Explain / Chat</div>

      <div style={{ display: "flex", gap: 12, marginBottom: 8, alignItems: "center" }}>
        <label>
          <input type="radio" checked={intent === "explain"} onChange={() => setIntent("explain")} />{" "}
          Explain
        </label>

        <label>
          <input type="radio" checked={intent === "trade"} onChange={() => setIntent("trade")} />{" "}
          Trade
        </label>

        {intent === "trade" && (
          <label style={{ marginLeft: 12 }}>
            <input type="checkbox" checked={ltfOnly} onChange={(e) => setLtfOnly(e.target.checked)} />{" "}
            LTF-only
          </label>
        )}
      </div>

      <textarea
        value={question}
        onChange={(e) => setQuestion(e.target.value)}
        placeholder={
          intent === "trade"
            ? "Describe the setup you want (short/long, context, constraints)…"
            : "Ask about structure, regime, EMA/RSI, HTF gate, what would change…"
        }
        rows={4}
        style={{ width: "100%", padding: 10, borderRadius: 8 }}
      />

      <div style={{ display: "flex", gap: 8, marginTop: 8 }}>
        <button onClick={ask} disabled={loading} style={{ padding: "8px 10px" }}>
          {loading ? "Running…" : intent === "trade" ? "Run Trade" : "Ask"}
        </button>

        <button
          onClick={() => {
            setQuestion("");
            setAnswer(null);
            setTradeResult(null);
            setError(null);
          }}
          disabled={loading}
          style={{ padding: "8px 10px" }}
        >
          Clear
        </button>
      </div>

      {error && <div style={{ marginTop: 10, color: "tomato" }}>{error}</div>}

      {answer && (
        <div style={{ marginTop: 10, whiteSpace: "pre-wrap", opacity: 0.95 }}>
          {answer}
        </div>
      )}

      {tradeResult && (
        <>
          <div style={{ marginTop: 10, whiteSpace: "pre-wrap", opacity: 0.95 }}>
            {formatTradeBrief(tradeResult)}
          </div>

          {/* Collapsible raw JSON to avoid UI blow-ups */}
          <details style={{ marginTop: 10 }}>
            <summary style={{ cursor: "pointer", opacity: 0.85 }}>Show raw JSON</summary>
            <pre
              style={{
                marginTop: 10,
                padding: 10,
                borderRadius: 8,
                background: "rgba(0,0,0,0.2)",
                fontSize: 12,
                overflowX: "auto",
                overflowY: "auto",
                maxHeight: 320, // key to prevent “distortion”
                whiteSpace: "pre-wrap", // allow wrapping
                wordBreak: "break-word",
              }}
            >
              {JSON.stringify(tradeResult, null, 2)}
            </pre>
          </details>
        </>
      )}
    </div>
  );
}
